<template>
  <div style="padding: 10px">

    <!--    搜索-->
    <div style="margin: 10px 0px;">
      <el-input v-model="search" placeholder="请输入姓名" clearable style="width:20%;margin-left: 950px"></el-input>
      <el-button type="primary" style="margin-left: 5px;" @click="load">查询</el-button>
    </div>
    <el-table :data="tableData" border style="width: 100%" stripe>
      <el-table-column prop="username" label="用户名" />
      <el-table-column prop="nickName" label="姓名" />
      <el-table-column prop="password" label="密码" />
      <el-table-column prop="age" label="年龄" />
      <el-table-column prop="sex" label="性别" />
      <el-table-column prop="tel" label="手机号" />
      <el-table-column prop="address" label="家庭住址" />
      <el-table-column label="操作">
        <template #default="scope">
          <el-button size="mini" @click="handleEidt(scope.row)">编辑</el-button>
          <el-popconfirm title="你确认删除吗?" @confirm="handleDelete(scope.row.id)">
            <template #reference>
              <el-button size="mini" type="danger">删除</el-button>
            </template>
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>

    <div style="margin: 10px 0px;float: right">
      <el-pagination
          v-model:currentPage="currentPage"
          :page-size="pageSize"
          :page-sizes="[5, 10, 20]"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
      />

      <el-dialog
          v-model="dialogVisible"
          title="修改病人信息"
          width="30%"
      >
        <el-form :model="form" label-width="120px">
          <el-form-item label="用户名">
            <el-input v-model="form.username" style="width: 160px " disabled></el-input>
          </el-form-item>
          <el-form-item label="昵称">
            <el-input v-model="form.nickName" style="width: 160px"></el-input>
          </el-form-item>
          <el-form-item label="密码">
            <el-input v-model="form.password" style="width: 160px"></el-input>
          </el-form-item>
          <el-form-item label="年龄">
            <el-input v-model="form.age" style="width: 160px"></el-input>
          </el-form-item>
          <el-form-item label="手机号">
            <el-input v-model="form.tel" style="width: 160px"></el-input>
          </el-form-item>
          <el-form-item label="性别">
            <el-radio v-model="form.sex" label="男">男</el-radio>
            <el-radio v-model="form.sex" label="女">女</el-radio>
            <el-radio v-model="form.sex" label="未知">未知</el-radio>
          </el-form-item>
          <el-form-item label="地址">
            <el-input type="textarea" v-model="form.address" style="width: 200px"></el-input>
          </el-form-item>
        </el-form>
        <template #footer>
          <span class="dialog-footer">
            <el-button @click="dialogVisible = false">取消</el-button>
            <el-button type="primary" @click="save">确定</el-button>
      </span>
        </template>
      </el-dialog>


    </div>
  </div>
</template>


<script>

import request from "@/utils/request";

export default {
  name: "Patient",
  data(){
    return{
      search:'',
      currentPage:1,
      pageSize:10,
      total:1,
      tableData:[],
      form:[],
      dialogVisible:false
    }
  },
  created() {
    this.load()
  },

  methods:{
    load(){
      request.get("/patient",{
        params:{
          pageNum:this.currentPage,
          pageSize:this.pageSize,
          search:this.search,
        }
      }).then(res => {
        this.tableData = res.data.records
        this.total = res.data.total
      })
    },

    save(){
      if(this.form.id){ //更新
        request.put("/patient",this.form).then(res => {
          if(res.code === '0'){
            this.$message({
              type:"success",
              message:"更新成功"
            })
          }else{
            this.$message({
              type:"error",
              message:res.message
            })
          }
          this.load() //刷新表格
          this.dialogVisible = false
        })
      }
    },

    handleSizeChange(val){ //改变每页个数
      this.pageSize = val
      this.load()
    },
    handleCurrentChange(val){  //改变当前页码
      this.currentPage = val
      this.load()
    },
    handleEidt(row){
      this.form = JSON.parse(JSON.stringify(row))
      this.dialogVisible = true
    },
    handleDelete(id){
      request.delete("/patient/"+id).then(res =>{
        if(res.code === '0'){
          this.$message({
            type:"success",
            message:"删除成功"
          })
        }else{
          this.$message({
            type:"error",
            message:res.message
          })
        }
        this.load() //刷新表格
      })
    },





  },

}
</script>

<style scoped>

</style>
